import MxDatePicker from "@/components/mx-datepicker/mx-datepicker.vue";
import luPopupWrapper from "@/components/lu-popup-wrapper/lu-popup-wrapper.vue";
export default {
	components:{
		MxDatePicker,
		luPopupWrapper
	},
	data() {
		return {
			addImg:false,
			types:false,
			typeChooe:"一起约",
			typeList:["一起约","约定我","约定他"],
			imgList:[],
			appointedTime:'请选择约定时间',
			showChooseTime:false,
			showNext:false,
			next:false,
			Appointment:true,
			AppointmentCryl:true,
			index:0,
			showCryl:false,
			showDetail:false,
			showDetail1:false,
							
			array:['三天一次','一周一次','两周一次','一月一次'],
			showPicker: false,
			date: '2019-01-01',
			time: '15:00:12',
			datetime: '2019-01-01 15:00:12',
			range: ['2019-01-01','2019/01/06'],
			rangetime: ['2019-01-01 14:00','2019-01-01 13:59'],
			type: 'rangetime',
			value: ''
		}
	},
	onLoad() {

	},
	methods: {
		//选择约定次数		
		frequency(type){
			if(type===1){
				this.Appointment = true
				this.showCryl = false
			}else if(type===2){
				this.Appointment = false
				this.showCryl = true
			} else if(type===3){
				this.AppointmentCryl = true
			} else{
				this.AppointmentCryl = false
			}
		},
		showD(){
			this.showDetail = true
		},
		//约定周期
		bindPickerChange(e) {
	        console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
		},
		
		onShowDatePicker(type){//显示
		    this.type = type;
		    this.showPicker = true;
		    this.value = this[type];
		},
		onSelected(e) {//选择
		    this.showPicker = false;
		    if(e) {
		        this[this.type] = e.value; 
		        //选择的值
				this.appointedTime = e.value
				this.showNext = true
		        //console.log('value => '+ e.value);
		         //原始的Date对象
		        //console.log('date => ' + e.date);
		    }
		},
		//判断是否输入内容
		yueContent(e){	
			this.ydContent = e.detail.value.replace(/\s*/g, "")
			if(this.ydContent.length>0){
				this.next = true
			}			
		},
		//选择约定谁
		openType(){
			this.types = !this.types
		},
		chooseType(item){
			this.typeChooe = item
			this.types = false
		},
		//选择时间
		show(){
			if(this.next == true){
				if(this.showNext == false){
					this.showChooseTime = true
					this.showDetail1=true
				}else{
					console.log(1)
				}			
			}else{
				uni.showToast({
				    title: '请输入约定内容',
				    duration: 1000
				});
			}
			
		},
			
		//弹出图片列表
		addImgs(){
			this.addImg = true
		},
		//选择上传图片
		chooseAlbum(){
			let that = this
			uni.chooseImage({
			    count: 3, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    //sourceType: ['album'], //从相册选择
			    success: function (res) {
					if(that.imgList.length<3){
						that.imgList = that.imgList.concat(res.tempFilePaths) 
					}else{
						uni.showToast({
						    title: '最多上传三张图片',
						    duration: 1000
						});
					}
					
			    },
				fail: () => {
			    	console.log('选择失败')
			    }
			});
		}
	}
}
